.page {
  font-family: "Poppins", sans-serif;
  background-color: #1d1d1d;
  color: $main-text-color;
  font-size: 24px;
  line-height: 140%;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    overflow: hidden;

    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0%);
      pointer-events: all;
    }
  }
}
